import { Button } from '@mui/material';

const CustomButton = ({
  value,
  className = '',
  type = 'submit',
  onClick = null,
  disable = false
}) => {
  return (
    <Button
      disabled={disable}
      variant="contained"
      type={type}
      onClick={onClick ? onClick : undefined}
      className={className}>
      {value}
    </Button>
  );
};

export default CustomButton;
